import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
    root: {
      width: '100%',
      textAlign: "left"
    },
  });

  export default function Education() {
    const classes = useStyles();
  
    return (
      <div className={classes.root}>
        
        <Typography variant="h4" gutterBottom>
          Education
        </Typography>
        
        <Typography variant="body1" gutterBottom>
        <ul>
            <li>2009-2013: Bachelor of Science (BSc) in Economics and Business. Specialization: Economics and Finance. Universiteit van Amsterdam.</li>
            <li>2018-2019: <a href="https://www.freecodecamp.org/certification/victorvermeulen/javascript-algorithms-and-data-structures" target="_blank">Developer Certification JavaScript Algorithms and Data Structures. FreeCodeCamp.</a></li>
            <li>2019: <a href="https://www.freecodecamp.org/certification/victorvermeulen/responsive-web-design" target="_blank">Developer Certification Responsive Web Design. FreeCodeCamp.</a></li>
        </ul>
        <br></br>
        </Typography>
        
      </div>
    );
  }